.content {
    display: flex;
    justify-content: center;
    height: 60vh;
    align-items: center;
    width: 100%;
  }
  
  .content .row {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .content .col {
    display: block;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .content input, .content button {
    border-radius: 4px;
    padding: 8px;
    margin: 10px 0;
    width: 250px;
    box-sizing: border-box;
  }
  
  .content button {
    background-color: #ecd69f;
    padding: 10px;
    border-radius: 8px;
    cursor: pointer;
    margin-bottom: 15px;
  }
  
  .content input {
    border: 1px solid #ccc;
  }
  
  .content input:focus, .content button:focus {
    outline: none;
  }
  
.recipe {
    list-style: none;
    padding: 10px;
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    height: 0vh;
    padding-bottom: 200px;
  }
  
 .recipe li {
    margin: 10px;
    padding: 15px;
    background-color: #343a40;
    border-radius: 8px;
    text-align: center;
    transition: background-color 0.3s;
    width: 150px;
  }
  
 .recipe li:hover {
    background-color: rgba(255, 255, 255, 0.5);
  }
  
 .recipe li a {
    color: white;
    text-decoration: none;
    font-weight: bold;
    font-size: 18px;
    display: block;
  }
  
 .recipe li a:hover {
    color: #007bff;
  }
  .error-box {
    background-color: #f8d7da;
    color: #721c24;
    border: 1px solid #f5c6cb;
    padding: 15px;
    margin-bottom: 20px;
    border-radius: 5px;
    font-size: 16px;
  }
.error p{
  background-color: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
  padding: 15px;
  margin-bottom: 20px;
  border-radius: 5px;
  font-size: 16px;
}
.loading-animation {
  font-size: 16px;
  color: #555;
  text-align: center;
  margin-top: 20px;
}

.loading-animation:after {
  content: '...';
  animation: dots 1.5s steps(3, end) infinite;
}

@keyframes dots {
  0% {
    content: '';
  }
  33% {
    content: '.';
  }
  66% {
    content: '..';
  }
  100% {
    content: '...';
  }
}

@media (max-width:980px){
  body{
    overflow: hidden;
  }
  content .col{
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
  }
  .content input{
    margin-bottom: 10px;
    padding:30px;
    font-size: 26px;
    width: 100%;
  }
  .content button{
    padding:20px;
    font-size: 26px;
    width: 250px;
    margin-bottom: 90px;
  }
  .recipe li a{
    font-size: 25px;
    padding-right: 20px;
  }
  .recipe{
    width: 100%;
  }
}